.buttonBlock{
    display: flex;
    justify-content: center;
    align-items: center;
    background: #E77E83;
    border-radius: 36px;
    width: 164px;
    height: 50px;
}
.button{
    font-weight: 600;
    font-size: 12px;
    line-height: 15px;
    letter-spacing: 1.5px;
    text-transform: uppercase;
    color: #FFFFFF;
}